export const onDiscountCreated = EventDefinition(
  'wix.restaurants.catalogs.v3.discount_created',
  true,
  (event: DiscountCreatedEnvelope) =>
    renameKeysFromRESTResponseToSDKResponse(
      transformPaths(event, [
        {
          transformFn: transformRESTTimestampToSDKTimestamp,
          paths: [{ path: 'metadata.eventTime' }],
        },
      ])
    )
)<DiscountCreatedEnvelope>();
export const onDiscountUpdated = EventDefinition(
  'wix.restaurants.catalogs.v3.discount_updated',
  true,
  (event: DiscountUpdatedEnvelope) =>
    renameKeysFromRESTResponseToSDKResponse(
      transformPaths(event, [
        {
          transformFn: transformRESTTimestampToSDKTimestamp,
          paths: [{ path: 'metadata.eventTime' }],
        },
      ])
    )
)<DiscountUpdatedEnvelope>();

